/*
 * Flexbox grid
 */
.grid {
    display: flex;
    flex-wrap: wrap;
}

.grid.\:horizontal {
  flex-direction: row;
}
.grid.\:vertical {
  flex-direction: column;
}

.grid.\:top {
    -ms-flex-align: start;
    -ms-grid-row-align: flex-start;
    align-items: flex-start
}

.grid.\:middle {
    -ms-flex-align: center;
    -ms-grid-row-align: center;
    align-items: center
}

.grid.\:bottom {
    -ms-flex-align: end;
    -ms-grid-row-align: flex-end;
    align-items: flex-end
}

.grid.\:baseline {
    -ms-flex-align: baseline;
    -ms-grid-row-align: baseline;
    align-items: baseline;
}

.grid.\:stretch {
    -ms-flex-align: stretch;
    -ms-grid-row-align: stretch;
    align-items: stretch
}

.grid.\:baseline {
    -ms-flex-align: baseline;
    -ms-grid-row-align: baseline;
    align-items: baseline
}

.grid.\:left {
    -ms-flex-pack: start;
    justify-content: flex-start
}

.grid.\:center {
    -ms-flex-pack: center;
    justify-content: center
}

.grid.\:right {
    -ms-flex-pack: end;
    justify-content: flex-end
}

.grid.\:between {
    -ms-flex-pack: justify;
    justify-content: space-between
}

.grid.\:around {
    -ms-flex-pack: distribute;
    justify-content: space-around
}

.cell {
    flex: 1;
    box-sizing: border-box
}

@media screen and (min-width:768px) {
    .cell.\:1of12 {
        -ms-flex: 0 0 8.33333%;
        flex: 0 0 8.33333%
    }
    .cell.\:2of12 {
        -ms-flex: 0 0 16.66667%;
        flex: 0 0 16.66667%
    }
    .cell.\:3of12 {
        -ms-flex: 0 0 25%;
        flex: 0 0 25%
    }
    .cell.\:4of12 {
        -ms-flex: 0 0 33.33333%;
        flex: 0 0 33.33333%
    }
    .cell.\:5of12 {
        -ms-flex: 0 0 41.66667%;
        flex: 0 0 41.66667%
    }
    .cell.\:6of12 {
        -ms-flex: 0 0 50%;
        flex: 0 0 50%
    }
    .cell.\:7of12 {
        -ms-flex: 0 0 58.33333%;
        flex: 0 0 58.33333%
    }
    .cell.\:8of12 {
        -ms-flex: 0 0 66.66667%;
        flex: 0 0 66.66667%
    }
    .cell.\:9of12 {
        -ms-flex: 0 0 75%;
        flex: 0 0 75%
    }
    .cell.\:10of12 {
        -ms-flex: 0 0 83.33333%;
        flex: 0 0 83.33333%
    }
    .cell.\:11of12 {
        -ms-flex: 0 0 91.66667%;
        flex: 0 0 91.66667%
    }

    .cell.\:12of12 {
        flex: 0 0 100%;
    }
}

@media screen and (max-width:768px) {
    .grid {
        -ms-flex-direction: column;
        flex-direction: column
    }
    .cell {
        -ms-flex: 0 0 auto;
        flex: 0 0 auto
    }
}
